import React from 'react'
import { Form, Button } from 'react-bootstrap'

const PersonForm = (props) => {
  const {
    handleNameChange,
    handleNumberChange,
    nameValue,
    numberValue,
    addName
  } = props

  return (
    <div>
    <Form onSubmit={addName}>
      <Form.Group>
        <Form.Label>Name:</Form.Label>
        <Form.Control
        type="text"
          onChange={handleNameChange}
          value={nameValue}
          placeholder="name"
          required
        />
        <Form.Label>Number:</Form.Label>
        <Form.Control
        type="text"
          onChange={handleNumberChange}
          value={numberValue}
          placeholder='number'         
        />
        <Button type="submit"
        style={{ margin: 10 }}
        >
        submit
        </Button>
      </Form.Group>
    </Form>
    </div>
  )
}

export default PersonForm