import React from 'react'

const Person = ({ name, number, handleRemoveButtonClick }) => {
  const buttonStyle = {
    borderRadius: 3
  }

  return (
    <tr>
      <td>{name}</td>
      <td>{number}</td>
      <td>
        <button 
        type="text"
         onClick={handleRemoveButtonClick}
         style={buttonStyle}
         >
        remove
        </button>
        </td>
    </tr>
  )
}

export default Person