import React from 'react'
import Person from './Person'
import { Table } from 'react-bootstrap'

const Persons = ({ persons, personFilter, handleRemoveButtonClick }) => {

  const usePersonFilter =
    personFilter ?
      persons.filter(person =>
        person.name.toLowerCase().startsWith(personFilter.toLowerCase())) :
      persons


  const showPersons = () => usePersonFilter.map(
    person => <Person
      key={person.name}
      name={person.name}
      number={person.number}
      handleRemoveButtonClick={handleRemoveButtonClick(person.id)}
    />
  )
  return (
    <Table striped>
      <tbody>
        {showPersons()}
      </tbody>
    </Table>
  )
}

export default Persons