import React, { useState, useEffect } from 'react'
import Persons from './components/Persons'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import personservices from './services/persons'
import Notification from './components/Notification'

const App = () => {
  const [persons, setPersons] = useState([])
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [personFilter, setPersonFilter] = useState('')
  const [notificationMessage, setNotificationMessage] = useState(null)

  useEffect(() => {
    personservices
      .getAll()
      .then(persons => {
        setPersons(persons)
      })
  }, [])

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }

  const handleFilterChange = (event) => {
    setPersonFilter(event.target.value)
  }

  const handleNotificationMessage = (message, isError) => {
    setNotificationMessage(
      { message: message, error: isError }
    )
    setTimeout(() => {
      setNotificationMessage(null)
    }, 5000)
  }

  const addName = (event) => {
    event.preventDefault()
    const find = persons.find(person => person.name === newName)
    // console.log(find)

    if (find) {
      if (window.confirm(find.name + " is allready in a catalog, replace old number?")) {
        const personObj = { name: newName, number: newNumber }
        personservices
          .update(find.id, personObj)
          .then(returnedPerson => {
            setPersons(persons.map(
              person => person.id !== returnedPerson.id ? person : returnedPerson))
            setNewName('')
            setNewNumber('')
            handleNotificationMessage('Number changed', false)
          })
      }
    } else {
      const personObj = { name: newName, number: newNumber }
      personservices
        .create(personObj)
        .then(returnedPerson => {
          setPersons(persons.concat(returnedPerson))
          setNewName('')
          setNewNumber('')
          handleNotificationMessage(`person ${personObj.name} added`, false)
        })
    }
  }

  const removePerson = (id) => () => {
    console.log(id)
    const person = persons.find(person => person.id === id)
    if (window.confirm("confirm remove of " + person.name)) {
      personservices
        .remove(id)
        .then(response => {
          setPersons(persons.filter(person => person.id !== id)
          )
          handleNotificationMessage(`${person.name} removed`, false)
        })
        .catch(error => {
          handleNotificationMessage(
            `person ${person.name} allready removed from server`, true)
            setPersons(persons.filter(person => person.id !== id))
        })
    }
  }

  return (
    <div className="container">
      <h2>Puhelinluettelo</h2>
      <Notification message={notificationMessage} />
      <Filter handleChange={handleFilterChange}
        value={personFilter}
      />
      <h3>lisää uusi</h3>
      <PersonForm
        handleNameChange={handleNameChange}
        handleNumberChange={handleNumberChange}
        nameValue={newName}
        numberValue={newNumber}
        addName={addName}
      />
      <h3>Numerot</h3>
      <Persons
        persons={persons}
        personFilter={personFilter}
        handleRemoveButtonClick={removePerson}
      />
    </div>
  )

}

export default App